os y se relacionan con los demás.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-3 col-sm-2 col-lg-1"},[_c('img',{attrs:{"src":require("@/assets/template/bg-icono-pdf.svg")}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col mb-3 mb-sm-0"},[_c('p',{staticClass:"text-bold mb-3"},[_vm._v("Desarrollo de los órganos sexuales primarios y secundarios")]),_c('p',{staticClass:"text-small"},[_vm._v("Lo invitamos a consultar el documento Desarrollo de los órganos sexuales primarios y secundarios para produndizar sobre el desarrollo de los órganos sexuales en los niños y adolescentes así como los cambios que se experimentan durante la pubertad.")])])}]

